import React, { Component } from 'react';
import { GoogleLogin } from 'react-google-login'
import GoogleButton from 'react-google-button'

import Modal from 'components/Modal'

import styles from './LoginModal.module.css'

export default class LoginModal extends Component {
  onResponse(response){
    console.log(response);
  }
  render() {
    return (
      <Modal
				onBackdropClicked={() => {
					this.props.onBackdropClicked()
				}}
			>
				<div className={styles.modalContainer}>
					<h3>Login</h3>
          <p>Only Google login currently available</p>
					<GoogleLogin
						clientId="258752383448-ggbh4bhjtq5glq2f623m4hjrf2g2ja67.apps.googleusercontent.com"
						buttonText="Login"
						onSuccess={this.onResponse.bind(this)}
						onFailure={this.onResponse.bind(this)}
						render={renderProps => <GoogleButton {...renderProps} />}
					/>
				</div>
			</Modal>
    );
  }
}


